export default {
	data() {
		return {
			title: 'Hello',
			active:false,
			value:'',
			list:[],
			activeIndex:0,
			text:'',
			textShow:false
		}
	},
	computed:{
		listData(){
			// 复制拷贝
			let list = JSON.parse(JSON.stringify(this.list))
			let newList = []
			// 全部
			if(this.activeIndex==0){
				this.text = '全部'
				return list;
				
			}
			// 待完成
			if(this.activeIndex==1){
				list.forEach((item)=>{
					if(!item.checked){
						newList.push(item);
					}
				})
				this.text = '待办';
				return newList;
			}
			// 已完成
			if(this.activeIndex==2){
				list.forEach((item)=>{
					if(item.checked){
						newList.push(item);
					}
				})
				this.text = '已完成';
				return newList;
				return []
			}
		}
	},
	onLoad() {

	},
	methods: {
		// 点击+号
		create(){
			if(this.active){
				this.close()
			}else{
				this.open();
			}	
		},
		// 打开动画
		open(){
			this.active=true;
			this.$nextTick(function(){
				setTimeout(()=>{
					this.textShow = true;
				},50)	
			})
		},
		// 关闭动画
		close(){
			this.textShow = false;
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.active = false;
				},350)
			})
		},
		// 发布
		add(){
			console.log(this.value);
			if(this.value===''){
				uni.showToast({
					title:"请输入内容",
					icon:'none'
				})
				return
			}
			this.list.unshift({
				id:'id'+new Date().getTime(),
				content:this.value,
				checked:false
			});
			this.value='';
			// this.active=false;
			this.close();
		},
		//点击列表触发
		finish(id){
			let index = this.list.findIndex((item)=>item.id === id)
			console.log(this.list[index]);
			this.list[index].checked= !this.list[index].checked;
		},
		tab(index){
			this.activeIndex = index;
		}
	}
}
